.home-page {
  padding: 2rem 1.5rem;
  padding-bottom: 100px;
}

.page-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 2rem;
}

.page-header h1 {
  font-size: 2rem;
  font-weight: 800;
  color: var(--text-primary);
  margin-bottom: 0.5rem;
}

.page-header p {
  color: var(--text-secondary);
}

.toggle-balance {
  width: 40px;
  height: 40px;
  border-radius: var(--radius-md);
  background: rgba(255, 255, 255, 0.1);
  border: none;
  color: var(--text-secondary);
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all var(--transition);
}

.toggle-balance:hover {
  background: rgba(255, 255, 255, 0.15);
  color: var(--text-primary);
}

.balance-card {
  background: linear-gradient(135deg, var(--purple-600), var(--purple-700));
  border-radius: var(--radius-xl);
  padding: 2rem;
  margin-bottom: 2rem;
  color: white;
  box-shadow: var(--shadow-lg);
}

.balance-header {
  font-size: 0.875rem;
  opacity: 0.9;
  margin-bottom: 0.5rem;
}

.balance-amount {
  font-size: 2.5rem;
  font-weight: 800;
  margin-bottom: 1.5rem;
}

.add-expense-btn {
  width: 100%;
  background: white;
  color: var(--purple-600);
  border: none;
  padding: 1rem;
  border-radius: var(--radius-md);
  font-weight: 700;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  cursor: pointer;
  transition: all var(--transition);
  margin-top: 1rem;
}

.add-expense-btn:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-md);
}

.card-section {
  margin-bottom: 2rem;
}

.card-section h2 {
  font-size: 1.25rem;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: 1rem;
}
