.card-visual {
  border-radius: var(--radius-xl);
  padding: 2rem;
  color: white;
  box-shadow: var(--shadow-lg);
  position: relative;
  overflow: hidden;
  min-height: 200px;
  transition: transform var(--transition);
}

.card-visual:hover {
  transform: translateY(-4px);
}

.card-visual::before {
  content: '';
  position: absolute;
  top: -50%;
  right: -20%;
  width: 300px;
  height: 300px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 50%;
  filter: blur(60px);
}

.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
  position: relative;
  z-index: 1;
}

.card-brand {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  font-size: 1.125rem;
  font-weight: 700;
}

.settings-btn {
  background: rgba(255, 255, 255, 0.2);
  border: none;
  border-radius: var(--radius-md);
  padding: 0.5rem;
  color: white;
  cursor: pointer;
  transition: all var(--transition);
  backdrop-filter: blur(10px);
}

.settings-btn:hover {
  background: rgba(255, 255, 255, 0.3);
  transform: scale(1.1);
}

.card-number {
  font-size: 1.5rem;
  font-weight: 600;
  letter-spacing: 0.15em;
  margin-bottom: 1.5rem;
  position: relative;
  z-index: 1;
  font-family: 'Courier New', monospace;
}

.card-details {
  display: flex;
  gap: 2rem;
  position: relative;
  z-index: 1;
}

.card-detail {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
}

.card-detail .label {
  font-size: 0.75rem;
  opacity: 0.8;
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

.card-detail .value {
  font-size: 1rem;
  font-weight: 600;
}

.card-chip {
  position: absolute;
  bottom: 2rem;
  left: 2rem;
  width: 50px;
  height: 40px;
  background: linear-gradient(135deg, #ffd700, #ffed4e);
  border-radius: var(--radius-sm);
  z-index: 1;
  box-shadow: var(--shadow-md);
}

.card-chip::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 40px;
  height: 30px;
  border: 2px solid rgba(0, 0, 0, 0.1);
  border-radius: 4px;
}
